import { NodeTracerProvider } from '@opentelemetry/sdk-trace-node';

// Configs
import { aspectoKafkaJsPluginConfig } from './kafkajs';
import { getAspectoAwsSdkPluginConfig } from './aws-sdk';
import { typeormPluginConfig, sequelizePluginConfig } from './orm';
import { aspectoIoredisPluginConfig, redisPluginConfig } from './redis';
import { awsLambdaInstrumentationConfig } from './aws-lambda';
import { aspectoHttpPluginConfig } from './http';
import { aspectoMongodbPluginConfig } from './mongodb';
import { aspectoMongoosePluginConfig } from './mongoose';
import { aspectoElasticsearchPluginConfig } from './elasticsearch';
import { expressInstrumentationConfig } from './express';
import { aspectoNeo4jInstrumentationConfig } from './neo4j';
import { aspectoAmqplibPluginConfig } from './amqplib';
import { aspectoSocketIoPluginConfig } from './socket.io';
import { nodeCacheInstrumentationConfig } from './node-cache';

// Instrumentations
import { registerInstrumentations } from '@opentelemetry/instrumentation';
import { HttpInstrumentation } from '@opentelemetry/instrumentation-http';
import { IORedisInstrumentation } from '@opentelemetry/instrumentation-ioredis';
import { AwsLambdaInstrumentation } from '@opentelemetry/instrumentation-aws-lambda';
import { AwsInstrumentation } from '@opentelemetry/instrumentation-aws-sdk';
import { ExpressInstrumentation } from 'opentelemetry-instrumentation-express';
import { SequelizeInstrumentation } from 'opentelemetry-instrumentation-sequelize';
import { TypeormInstrumentation } from 'opentelemetry-instrumentation-typeorm';
import { KafkaJsInstrumentation } from 'opentelemetry-instrumentation-kafkajs';
import { MongooseInstrumentation } from 'opentelemetry-instrumentation-mongoose';
import { ElasticsearchInstrumentation } from 'opentelemetry-instrumentation-elasticsearch';
import { Neo4jInstrumentation } from 'opentelemetry-instrumentation-neo4j';
import { AmqplibInstrumentation } from '@opentelemetry/instrumentation-amqplib';
import { SocketIoInstrumentation } from 'opentelemetry-instrumentation-socket.io';
import { MongoDBInstrumentation } from '@aspecto/opentelemetry-instrumentation-mongodb';
import { NodeCacheInstrumentation } from 'opentelemetry-instrumentation-node-cache';
import { RedisInstrumentation as RedisInstrumentationV2 } from '@opentelemetry/instrumentation-redis';
import { RedisInstrumentation as RedisInstrumentationV4 } from '@opentelemetry/instrumentation-redis-4';
import { BunyanInstrumentation } from '@opentelemetry/instrumentation-bunyan';
import { PinoInstrumentation } from '@opentelemetry/instrumentation-pino';
import { WinstonInstrumentation } from '@opentelemetry/instrumentation-winston';
import { NestInstrumentation } from '@opentelemetry/instrumentation-nestjs-core';

export const loadPlugins = (tracerProvider: NodeTracerProvider) => {
    registerInstrumentations({
        tracerProvider,
        instrumentations: [
            new ExpressInstrumentation(expressInstrumentationConfig),
            new SequelizeInstrumentation(sequelizePluginConfig),
            new KafkaJsInstrumentation(aspectoKafkaJsPluginConfig),
            new AwsInstrumentation(getAspectoAwsSdkPluginConfig()),
            new TypeormInstrumentation(typeormPluginConfig),
            new MongooseInstrumentation(aspectoMongoosePluginConfig),
            new ElasticsearchInstrumentation(aspectoElasticsearchPluginConfig),
            new HttpInstrumentation(aspectoHttpPluginConfig),
            new Neo4jInstrumentation(aspectoNeo4jInstrumentationConfig),
            new AmqplibInstrumentation(aspectoAmqplibPluginConfig),
            new SocketIoInstrumentation(aspectoSocketIoPluginConfig),
            new IORedisInstrumentation(aspectoIoredisPluginConfig),
            new MongoDBInstrumentation(aspectoMongodbPluginConfig),
            new AwsLambdaInstrumentation(awsLambdaInstrumentationConfig),
            new NodeCacheInstrumentation(nodeCacheInstrumentationConfig),
            new RedisInstrumentationV2(redisPluginConfig),
            new RedisInstrumentationV4(redisPluginConfig),
            new NestInstrumentation(),

            // loggers
            new BunyanInstrumentation(),
            new PinoInstrumentation(),
            new WinstonInstrumentation(),
        ],
    });
};
